.battle-top-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

h3{
    color: red;
}

.user-sprite{
    -ms-flex-positive: 1;
        flex-grow: 1;
    -webkit-transform: translate(150px, 80px);
        -ms-transform: translate(150px, 80px);
            transform: translate(150px, 80px);
}

.user-sprite img{
    width: 300px;
    height:300px;
}

.user-sprite .player-name{
    margin-left: 5%;
}

.monster-sprite{
    -ms-flex-positive: 1;
        flex-grow: 1;
    -webkit-transform: translateY(30px);
        -ms-transform: translateY(30px);
            transform: translateY(30px);
}

.monster-sprite .monster-name{
    margin-left: 40px;
}

.monster-sprite img{
    width: 300px;
    height: 300px;  
}

.move-user {
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    -webkit-transform: translateX(470px);
        -ms-transform: translateX(470px);
            transform: translateX(470px);
}


.move-monster .hit-box{
    width: 250px;
    height: 250px;
    background-color: red;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.5;
    /* transform: translate(850px, 140px); */
    border-radius: 50%;
}

.hit-box h1{
    visibility: hidden;
}

.move-monster .hit-box h1{
    visibility: visible;
    font-size: 60px;
    color: white;
    -webkit-transform: translate(70px, 30px);
        -ms-transform: translate(70px, 30px);
            transform: translate(70px, 30px);
}

.hit-ui{
    position: absolute;
    left: 62%;
    top: 30%;
}


